import React, {Component} from 'react';
import axios from 'axios';
import Chart from './chart';
import CryptoItem from './cryptoItem';
import './App.css';

let DATA = [
  {
    currency: 'BTC',
    image: 'https://www.cobrabitcoin.com/images/moneda-bitcoin.png',
    prices: [],
    history: []
  }, {
    currency: 'ETH',
    image: 'http://www.coins2day.com/images/altcoins/ethereum.png',
    prices: [],
    history: []
  }, {
    currency: 'DASH',
    image: 'http://bitcoinist.com/wp-content/uploads/2016/01/Dash-logo.png',
    prices: [],
    history: []
  }
];

const CurrencyComponent = ({data}) => (
  <ul className="container_currency">
    {data.map((coin, index) =>< CryptoItem key = {
      index
    }
    currency = {
      coin
    } />)
}
  </ul>
);

class ChartContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      valid: this.props.paramsFilter && this.props.limit
    }
  }
  render() {
    const {paramsFilter, limit} = this.props;

    if (paramsFilter && limit) {

      if (!this.state.valid) {
        this.setState({valid: true});
      }

    }
    return (
      <section className="chartContainer">
        {(this.state.valid) && <Chart data={this.props.data} rangeDescriptions={this.props.rangeDescriptions}/>}
      </section>
    );

  }
}

class FilterActions extends Component {
  constructor(props) {
    super(props);

    this.state = {}
  }
  componentWillUpdate(nextProps, nextState) {
    const {interval, range} = nextState;
    if (interval && range) {}
  }

  handleChange(event, params) {
    if (params) {
      this.setState({range: event.range});
    } else {
      this.setState({
        interval: parseInt(event.target.value)
      });
    }

    const {interval, range} = this.state;
    if (interval || range) {
      this
        .props
        .FilterActions(Object.assign(this.state, event));
    }
  }
  render() {
    const options = (
      <div style={{
        display: 'inline-block'
      }}>
        <li className="list_items_actions">
          <button
            className="btn_see"
            onClick={this
            .handleChange
            .bind(this, {range: 'day'})}>Day</button>
        </li>
        <li className="list_items_actions">
          <button
            className="btn_see"
            onClick={this
            .handleChange
            .bind(this, {range: 'month'})}>Months</button>
        </li>
        <li className="list_items_actions">
          <button
            className="btn_see"
            onClick={this
            .handleChange
            .bind(this, {range: 'year'})}>Years</button>
        </li>
      </div>
    );

    return (
      <section>
        <ul className="list_actions">
          <li className="list_items_actions">Last (min:3,max:5)</li>
          <li className="list_items_actions"><input
            type="number"
            min="3"
            max="5"
            value={this.state.interval}
            onChange={this
        .handleChange
        .bind(this)}
            className="input_action"
            placeholder="type Num" autofocus/></li>
          {this.state.interval && options
}
        </ul>
      </section>
    )
  }
}

class App extends Component {
  constructor() {
    super()
    this.state = {
      data: DATA
    }
  }
  componentDidMount() {

    axios
      .get('/api')
      .then(({data}) => {
        data.map((c, k) => {
          const {currency, price: Actual} = c;
          let state = [...this.state.data],
            currenObject = state.find(current => current.currency === currency),
            index = state.findIndex(current => current.currency === currency);
          if (currenObject.currency) {
            currenObject.currency = currency;
            currenObject.price = Actual.USD;
            currenObject.prices = Object.entries(Actual);
            state[index] = currenObject;
            this.setState({data: state})

          }

        })
      })

  }
  FilterActions(state) {
    const {interval, range} = state;
    if (['year', 'month', 'day', 'hour'].some(i => i === range) && interval) {
      const rangeDescriptions = [...Array(interval).keys()]
        .reverse()
        .map(each => (each===0)?`now`:`${each} - ${range} ago`);
      this.setState({rangeDescriptions});

      axios
        .get(`/api/${range}/${interval}`)
        .then(({data}) => {
          data.map((c, k) => {
            const {currency, prices: History} = c;
            let state = [...this.state.data],
              currenObject = state.find(current => current.currency === currency),
              index = state.findIndex(current => current.currency === currency);
            currenObject.history = History;
            state[index] = {
              ...currenObject
            };
            this.setState(state);
            this.setState({filter: range, limit: interval});
          },)
        });

    }

  }

  render() {
    return (
      <div className="panel">
        <CurrencyComponent data={this.state.data}/>
        <FilterActions
          FilterActions={this
          .FilterActions
          .bind(this)}/>
        <ChartContainer
          data={this.state.data}
          paramsFilter={this.state.filter}
          limit={this.state.limit}
          rangeDescriptions={this.state.rangeDescriptions}/>
      </div>
    );
  }
}

export default App;



