import React, {Component} from 'react';
import {Line} from 'react-chartjs-2';

class Chart extends Component {
    constructor(props) {
        super(props);
        const [BTC,
            ETH,
            DASH] = this.props.data;
        const dates = this.props.rangeDescriptions;
        this.state = {
            data: {
                labels: dates,
                datasets: [
                    {
                        label: BTC.currency,
                        data: BTC
                            .history
                            .map((each, index) => {
                                return {y: each.price}
                            }),
                        borderColor: ['#feb13e'],
                        backgroundColor: ['rgba(0,0,0,0)']
                    }, {
                        label: ETH.currency,
                        data: ETH
                            .history
                            .map((each, index) => {
                                return {y: each.price}
                            }),
                        borderColor: ['#333333'],
                        backgroundColor: ['rgba(0,0,0,0)']
                    }, {
                        label: DASH.currency,
                        data: DASH
                            .history
                            .map((each, index) => {
                                return {y: each.price}
                            }),
                        borderColor: ['#1a74bb'],
                        backgroundColor: ['rgba(0,0,0,0)']
                    }
                ]
            }
        }
    }
    componentWillReceiveProps = (nextProps) => {

        nextProps
            .data
            .map((each, index) => {
                let stateOld = {
                    ...this.state.data
                }

                let dataSet = stateOld
                    .datasets
                    .find((o) => o.label === each.currency)
                let dataSetIndex = stateOld
                    .datasets
                    .findIndex((o) => o.label === each.currency)
                dataSet.data = each
                    .history
                    .map(t => Object.assign({}, {y: t.price}));
                stateOld.datasets[dataSetIndex] = dataSet;
                this.setState({data: stateOld});

            });
    }

    render() {
        if (this.state.data.labels != this.props.rangeDescriptions) {
            const old = this.state.data;
            old.labels = this.props.rangeDescriptions;
            this.setState({data: old})
        }
        return (
            <div
                className="chart"
                style={{
                backgroundColor: '#ffffff'
            }}>
                <Line
                    data={this.state.data}
                    options={{
                    title: {
                        display: 'Walmart Chart',
                        text: 'History Price',
                        fontSize: 22
                    }
                }}/>
            </div>
        )
    }
}
export default Chart;


