import React, {Component} from 'react';
class CryptoItem extends Component {
    constructor(props){
        super(props);
        this.state = {
            currency:{}
        };
    }
    formatPrice(){
        
    }
    componentWillMount(){
      this.setState({currency:this.props.currency})
    }
    
    render() {
        const {image,currency:name,price,prices} = this.state.currency;

        return (
            <div className="box_currency">
                <div className="section_description">
                    <div><img className="coin_item_image" src={image}/></div>
                    <div>
                        <span className="coin_item_name">{name}</span>
                    </div>
                </div>
                <div className="section_price">
                    <span>{`$ ${Number(price)
                            .toFixed(2)
                            .replace(/(\d)(?=(\d{3})+\.)/g, '$1,')}`}</span>
                </div>

                <div className="section_prices">
                    <ul className="list_price">
                        {prices.map((each,key)=>(<li key={key}>{`${each[0]}: ${Number(each[1])
                            .toFixed(2)
                            .replace(/(\d)(?=(\d{3})+\.)/g, '$1,')}`}</li>))}
                    </ul>
                </div>

            </div>
        );
    }
}

export default CryptoItem;


